<template>
  <div
    class="
      w-full
      py-4
      max-w-sm
      transition-all
      duration-500
      mx-auto
      rounded-md
      shadow-md
      overflow-hidden
    "
  >
    <img
      v-lazy="{
        src: lazyOptions.src[0],
        lifecycle: lazyOptions.lifecycle,
      }"
      class="w-full urlimg justify-end items-end flex h-56 bg-cover"
    />

    <button
      class="rounded-full mt-6 mx-auto block p-2"
      :class="
        isAdded
          ? 'bg-red-600 hover:bg-red-500 focus:bg-red-500'
          : 'bg-blue-600 hover:bg-blue-500 focus:bg-blue-500'
      "
      @click="addtoCart(product)"
    >
      <font-awesome-icon
        class="text-white text-3xl"
        v-if="!isAdded"
        :icon="['fas', 'cart-plus']"
      />
      <font-awesome-icon
        class="text-white text-3xl"
        v-else
        :icon="['fas', 'backspace']"
      />
    </button>

    <div class="py-3 px-3">
      <h3 class="text-center text-white">{{ product.title }}</h3>
      <p class="rounded-full text-white">price : {{ product.price }} $</p>
      <img
        v-lazy="{
          src: lazyOptions.src[1],
          lifecycle: lazyOptions.lifecycle,
        }"
        class="
          rounded-full
          urlimg
          justify-end
          items-end
          inline
          h-10
          w-10
          bg-cover
        "
      />
      <div class="w-2 h-2 inline-block ml-2 bg-green-500 rounded-full"></div>
      <button class="text-center block mx-auto">
        <router-link
          class="p-2 text-green-700 hover:text-green-500 rounded-lg"
          :to="'/product/' + product._id"
          >show Details</router-link
        >
      </button>
    </div>
  </div>
</template>

<script>
import SocketioService from "../../services/socketIo.service";
import { ref, computed, reactive } from "vue";
import store from "@/store/index.js";
export default {
  props: {
    product: {
      type: Object,
      require: true,
    },
  },
  methods: {},
  setup(props) {
    const isAdded = ref(false);
    const cart = computed(() => {
      return store.state.product.cart;
    });
    function addtoCart(product) {
      isAdded.value = !isAdded.value;
      if (isAdded.value) {
        store.commit("product/addtoCart", product);
        store.commit("product/storedProduct");
      } else {
        store.commit("product/removeProduct", product);
        store.commit("product/storedProduct");
      }
    }
    function checkAdedd() {
      var product = props.product._id;

      var check = cart.value.some((el) => {
        return el._id == product;
      });
      if (check) {
        isAdded.value = true;
      }
    }
    function inlineBgImage(src) {
      let bgImage = require("../assets/uploads/" + src);

      return bgImage;
    }
    const lazyOptions = reactive({
      src: [
        inlineBgImage(props.product.img_url),
        inlineBgImage(props.product.user_img),
      ],
      lifecycle: {
        loading: () => {
          console.log("image loading");
        },
        error: () => {
          console.log("image error");
        },
        loaded: () => {
          console.log("image loaded");
        },
      },
    });
    return {
      cart,
      addtoCart,
      isAdded,
      checkAdedd,
      lazyOptions,
    };
  },
  mounted() {
    this.checkAdedd();
    SocketioService.setupSocketConnection();
  },
};
</script>
