<template>
  <div>
    <h3 class="text-gray-500 mx-auto container underline shadow-md">
      add new user <font-awesome-icon :icon="['fas', 'user-plus']" />
    </h3>
  </div>
  <div class="flex items-center justify-center m-auto">
    <div class="p-2 w-full lg:w-3/6 mx-auto">
      <input
        type="text"
        disabled
        v-model="status"
        class="text-green-500 w-full border-0"
      />
      <form @submit.prevent="signup" class="flex flex-col w-full mx-auto">
        <input
          v-model="user.username"
          type="text"
          placeholder="username"
          class="
            w-full
            hover:bg-blue-200
            my-2
            border-gray-400 border-2
            hover:border-blueGray-600
          "
        />
        <input
          v-model="user.password"
          type="password"
          placeholder="password"
          class="
            w-full
            hover:bg-blue-200
            border-gray-400 border-2
            hover:border-blueGray-600
          "
        />
        <input
          v-model="user.email"
          type="text"
          placeholder="email"
          class="
            w-full
            hover:bg-blue-200
            my-2
            border-gray-400 border-2
            hover:border-blueGray-600
          "
        />
        <input
          type="submit"
          class="
            bg-gray-600
            mt-6
            cursor-pointer
            text-white
            p-2
            w-4/12
            mx-auto
            rounded-lg
          "
        />
      </form>
      <div
        v-if="message"
        class="alert"
        :class="successful ? 'text-green-500' : 'text-red-400'"
      >
        {{ message }}
      </div>
    </div>
  </div>
</template>

<script>
import User from "../models/user";
export default {
  data: () => {
    return {
      user: new User("", "", ""),
      message: "",
      successful: false,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push("/profile");
    }
  },

  methods: {
    signup: function () {
      this.message = "";

      this.$store.dispatch("auth/register", this.user).then(
        (data) => {
          this.message = data.message;
          this.successful = true;
        },
        (error) => {
          this.successful = false;
          this.message =
            (error.response && error.response.data.message) ||
            error.message ||
            error.message.toString();
        }
      );
    },
  },
};
</script>

<style></style>
