<template>
  <div class="container w-full relative mx-auto px-6">
    <div>
      <h3 class="text-gray-500 mx-auto container font-font3 shadow-md">
        products ( {{ Products.length }} )
      </h3>
    </div>

    <div>
      <select ref="filterP" @change="filteredProducts" name="" id="">
        <option value="All">ALL</option>
        <option value="phone">phone</option>
        <option value="close">close</option>
        <option value="pc">pc</option>
      </select>
    </div>
    <div
      class="
        grid
        gap-6
        grid-cols-1
        transition-all
        duration-500
        sm:grid-cols-3
        xl:grid-cols-4
        mt-6
      "
    >
      <Products
        :product="product"
        v-for="(product, index) in Productss"
        :key="index"
      />
    </div>
  </div>
</template>

<script>
import Products from "@/components/Products";
import { ref } from "vue";
import axios from "axios";
export default {
  data() {
    return {
      Productss: [],
    };
  },
  components: {
    Products,
  },
  methods: {
    filteredProducts: function () {
      this.Productss = [];

      if (
        this.Products.filter((product) => {
          if (product.catogres == this.$refs.filterP.value) {
            return this.Productss.push(product);
          }
        }).length > 0
      ) {
        return;
      } else {
        this.Productss = this.Products;
      }
    },
  },
  setup() {
    const Products = ref([]);
    function getProducts() {
      axios
        .get("/product")
        .then((res) => {
          Products.value = res.data;
          this.Productss = res.data;
        })
        .catch((err) => console.log(err));
    }
    return {
      Products,
      getProducts,
    };
  },

  mounted() {
    this.getProducts();
  },
};
</script>
<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: all 1s;
}
.fade-enter, .fade-leave-active /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
