<template>
  <div class="container mt-16 w-full">
    <div class="user">
      <div class="flex justify-center">
        <img
          class="rounded-full w-32 h-32 shadow-xl"
          :src="imgupload(currentUser.urlimg)"
        />
      </div>
      <div
        class="
          w-1/6
          mx-auto
          text-white
          mt-2
          text-xl
          font-font3 font-semibold
          text-center
        "
      >
        {{ currentUser.username }}
      </div>
      <div
        class="w-1/6 mx-auto mt-2 text-xl font-font3 font-semibold text-center"
      >
        <form enctype="multipart/form-data" @submit.prevent="uploadfile">
          <input @change="selectfile" type="file" ref="fileii" />

          <input
            type="submit"
            value="chang your photo"
            class="
              rounded-md
              p-2
              bg-emerald-600
              text-white
              mt-6
              text-center
              font-font1
            "
          />
        </form>

        <div>{{ msg }}</div>
      </div>
    </div>
    <div>
      <img
        v-if="file"
        class="mt-16 ml-10 w-full"
        :src="imgupload(currentUser.urlimg)"
      />
    </div>
  </div>
</template>

<script>
import SocketioService from "../../services/socketIo.service";
export default {
  name: "Profile",

  data: function () {
    return {
      msg: null,
      file: "",
    };
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
    SocketioService.setupSocketConnection();
  },
  methods: {
    imgupload(src) {
      let bgImage = require("../assets/uploads/" + src);
      // let bgImage = src
      return bgImage;
    },

    selectfile() {
      this.file = this.$refs.fileii.files[0];
      console.log(this.file);
    },
    uploadfile() {
      const formData = new FormData();
      formData.append("file", this.file);
      formData.append("id", this.currentUser.id);
      this.$store.dispatch("auth/uploadImgProfile", formData).then(
        () => {
          // this.$router.push("/products");
          this.$router.go();
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response && error.response.data.message) ||
            error.message ||
            error.toString();
        }
      );
    },
  },
};
</script>
